import ArrowRightIcon from 'vue-material-design-icons/ArrowRight.vue';
import AssistantIcon from '../../components/icons/AssistantIcon.vue';
import NcButton from '@nextcloud/vue/dist/Components/NcButton.js';
import NcLoadingIcon from '@nextcloud/vue/dist/Components/NcLoadingIcon.js';
import NcCheckboxRadioSwitch from '@nextcloud/vue/dist/Components/NcCheckboxRadioSwitch.js';
import VueAudioRecorder from 'vue2-audio-recorder';
import axios from '@nextcloud/axios';
import { generateUrl } from '@nextcloud/router';
import { getFilePickerBuilder, showError, showSuccess } from '@nextcloud/dialogs';
import Vue from 'vue';
Vue.use(VueAudioRecorder);
const VALID_MIME_TYPES = ['audio/mpeg', 'audio/mp4', 'audio/ogg', 'audio/wav', 'audio/x-wav', 'audio/webm', 'audio/opus', 'audio/flac', 'audio/vorbis', 'audio/m4b'];
const picker = getFilePickerBuilder(t('assistant', 'Choose Audio File')).setMimeTypeFilter(VALID_MIME_TYPES).setMultiSelect(false).allowDirectories(false).setType(1).build();
export default {
  name: 'SpeechToTextCustomPickerElement',
  components: {
    ArrowRightIcon,
    NcButton,
    NcCheckboxRadioSwitch,
    NcLoadingIcon,
    AssistantIcon
  },
  props: {
    providerId: {
      type: String,
      required: true
    },
    accessible: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      loading: false,
      mode: 'record',
      audioData: null,
      audioFilePath: null
    };
  },
  methods: {
    resetAudioState() {
      this.audioData = null;
      this.audioFilePath = null;
    },
    async onChooseButtonClick() {
      this.audioFilePath = await picker.pick();
    },
    async onRecordEnd(e) {
      try {
        this.audioData = e.blob;
      } catch (error) {
        console.error('Recording error:', error);
        this.audioData = null;
      }
    },
    async onInputEnter() {
      if (this.mode === 'record') {
        const url = generateUrl('/apps/assistant/stt/transcribeAudio');
        const formData = new FormData();
        formData.append('audioData', this.audioData);
        await this.apiRequest(url, formData);
      } else {
        const url = generateUrl('/apps/assistant/stt/transcribeFile');
        const params = {
          path: this.audioFilePath
        };
        await this.apiRequest(url, params);
      }
      this.resetAudioState();
    },
    async apiRequest(url, data) {
      this.loading = true;
      try {
        await axios.post(url, data);
        showSuccess(t('assistant', 'Successfully scheduled transcription'));
        this.$emit('submit', '');
      } catch (error) {
        console.error('API error:', error);
        this.resetAudioState();
        showError(t('assistant', 'Failed to schedule transcription') + (': ' + error.response?.data || error.message || t('assistant', 'Unknown API error')));
      } finally {
        this.loading = false;
      }
    }
  }
};